import React from "react";
import {
  PanelHeader,
  FormLayout,
  FormLayoutGroup,
  Input,
} from "@vkontakte/vkui";

class FeedPanel extends React.Component {
  render() {
    return (
      <React.Fragment>
        <PanelHeader>Матвей</PanelHeader>
        <FormLayout>
          <FormLayoutGroup
            top="Пароль"
            bottom="Пароль может содержать только латинские буквы и цифры."
          >
            <Input type="password" placeholder="Введите пароль" />
            <Input type="password" placeholder="Повторите пароль" />
          </FormLayoutGroup>
        </FormLayout>
      </React.Fragment>
    );
  }
}

export default FeedPanel;
