import React from "react";
import {
  PanelHeader,
  FormLayout,
  FormLayoutGroup,
  Input,
} from "@vkontakte/vkui";
import Icon16Dropdown from "@vkontakte/icons/dist/16/dropdown";
import Icon28ArrowUpOutline from "@vkontakte/icons/dist/28/arrow_up_outline";
import Icon16Cancel from '@vkontakte/icons/dist/16/cancel';

class CreatePost extends React.Component {
  render() {
    return (
      <React.Fragment>
        <PanelHeader
          left={
            <Icon16Cancel
              onClick={() => {
                this.props.changePanel("feed");
              }}
            />
          }
          right={
            <Icon28ArrowUpOutline
              onClick={() => {
                this.props.changePanel("feed");
              }}
            />
          }
        >
          Матвей <Icon16Dropdown />
        </PanelHeader>

        <FormLayout>
          <FormLayoutGroup
            top="Пароль"
            bottom="Пароль может содержать только латинские буквы и цифры."
          >
            <Input type="password" placeholder="Введите пароль" />
            <Input type="password" placeholder="Повторите пароль" />
          </FormLayoutGroup>
        </FormLayout>
      </React.Fragment>
    );
  }
}

export default CreatePost;
